.container{
  height:100% !important;
  width: 100% !important;
}

#root{
  height: 100% !important;
  width: 100% !important;
}

.gameBackground {
    /* The image used */
    background-image: url("../images/gameBackground.jpg") !important;
  
    /* Full height */
    height: 100% !important;
    width: 100% !important;
  
    /* Center and scale the image nicely */
    background-repeat: no-repeat !important;
    background-size: cover !important;
  }
  
  .inGameHeading {
    font-family: "Almendra SC";
    font-size: 40px;
    color: black;
    background: transparent !important;
    position: absolute !important;
    top: 5% !important;
    left: 5% !important;
    text-shadow: 0 0 3px #fff, 0 0 6px #fff, 0 0 9px #164f29,
        0 0 15px #164f29, 0 0 20px #164f29, 0 0 25px #164f29, 0 0 30px #164f29;
  }

  .baseText {
    font-family: "Almendra SC";
    font-weight: 700;
    font-size: 25px;
    color: black;
  }

  .armourSlot{
  font-family: "Almendra SC" !important;
  position: absolute !important;
  background-color: rgba(74, 103, 83, 0.872) !important;
  height: 18%;
  width: 9%;
  font-size: 20px !important;
  color: black !important;
  border-radius: 3px solid transparant;
  box-shadow: 
  0 2px 4px #fff, /*bottom external highlight*/
  0 -2px 2px #666, /*top external shadow*/ 
  inset 0 -1px 1px rgba(0,0,0,0.5), /*bottom internal shadow*/ 
  inset 0 1px 1px rgba(255,255,255,0.8)!important;/*top internal highlight*/
  }